<script>
import { mapState, mapActions } from 'vuex'
import Utils from '@/utils/util'
import viewFIle from '@/components/Modal/ViewFile.vue'
export default {
  name: 'DetailApproval',
  data () {
    return {
      notifications: false,
      sound: true,
      widgets: false,
      alignments: [
        'start',
        'center',
        'end'
      ]
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  components: {
    viewFIle
  },
  computed: {
    ...mapState('approval', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('approval', [
      'dialogClose', 'postSubmit'
    ]),
    ...mapActions('modalFile', [
      'setFile'
    ]),
    toRupiahString (nominal) {
      return Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    checkFile (file) {
      if (file) {
        const ext = file.split('.')
        if (ext[4] === 'pdf') {
          return true
        } else {
          return false
        }
      }
      return true
    }
  }
}
</script>
