<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Approval
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.SubmitDate`]="{ item }">
            {{ toDateTime(item.SubmitDate) }}
        </template>
        <template #[`item.firstname`]="{ item }">
            {{ item.firstname }} {{ item.lastname }}
        </template>
        <template #[`item.DSR`]="{ item }">
            {{ item.DSR ? 'Ya' : 'Tidak' }}
        </template>
        <template #[`item.SP`]="{ item }">
            {{ item.SP ? 'Ya' : 'Tidak' }}
        </template>
        <template #[`item.LoanAmount`]="{ item }">
            {{ toRupiahString(item.LoanAmount > 0 ? item.LoanAmount : '0')}}
        </template>
        <template #[`item.Angsuran`]="{ item }">
            {{ toRupiahString(item.Angsuran > 0 ? item.Angsuran : '0')}}
        </template>
        <template #[`item.TotalPencairan`]="{ item }">
            {{ toRupiahString(item.TotalPencairan > 0 ? item.TotalPencairan : '0')}}
        </template>
        <template #[`item.ApvSekretaris`]="{ item }">
          <v-chip v-show="item.ApvSekretaris"
            class="ma-2"
            color="success"
            text-color="white"
            small
          >
          Disetujui
          </v-chip>
        </template>
        <template #[`item.ApvKetua`]="{ item }">
          <v-chip v-show="item.ApvKetua"
            class="ma-2"
            color="success"
            text-color="white"
            smal
          >
          Disetujui
          </v-chip>
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn v-if="item.ApvSekretaris > 0 || item.ApvKetua > 0" depressed elevation="2" :loading="false" color="secondary" disabled>
            DONE
          </v-btn>
          <v-btn v-else depressed elevation="2" :loading="false" color="primary" @click="handledetail(item)">
            Approval
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <detailApproval/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import detailApproval from './../components/Approval/Detail.vue'
import store from '@/store'
import Utils from '@/utils/util'
export default {
  name: 'OrganizationPage',
  components: {
    detailApproval
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: 'Tgl Pengajuan',
        align: 'start',
        sortable: false,
        value: 'SubmitDate',
        width: '130px'
      },
      { text: 'Nama', value: 'firstname', sortable: false },
      { text: 'NIK', value: 'NIK', sortable: false },
      { text: 'Lokasi Kerja', value: 'WorkLocation', sortable: false },
      { text: 'Jenis Pengajuan', value: 'JenisPengajuan', sortable: false },
      { text: 'Pinjaman', value: 'LoanAmount', sortable: false, align: 'right' },
      { text: 'Angsuran', value: 'Angsuran', sortable: false, align: 'right' },
      { text: 'Total Pencairan', value: 'TotalPencairan', sortable: false, align: 'right' },
      { text: 'DSR', value: 'DSR', sortable: false },
      { text: 'SP', value: 'SP', sortable: false },
      { text: 'Sekretaris', value: 'ApvSekretaris', sortable: false },
      { text: 'Ketua', value: 'ApvKetua', sortable: false },
      { text: 'Action', value: 'action', sortable: false }
    ]
  }),
  mounted: function () {
    store.dispatch('approval/reqData')
  },
  computed: {
    ...mapState('approval', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('approval', [
      'edit', 'handledetail'
    ]),
    toRupiahString (nominal) {
      return Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateStringMount(tanggal)
    }
  }
}
</script>
