<template>
  <div class="text-center">
    <v-dialog
      v-model="state.dialog"
      width="100%"
      max-width="550"
    >
      <v-card>
        <iframe width="100%" height="500px" v-if="checkFile(state.file) && state.file"
        :src="state.file"></iframe>
        <v-img v-else width="100%" height="100%" :src="state.file"></v-img>

        <v-divider></v-divider>

        <div>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="state.dialog = false"
          >
            Close
          </v-btn>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
export default {
  data () {
    return {
      dialog: false
    }
  },
  computed: {
    ...mapState('modalFile', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('modalFile', [
      'simpan'
    ]),
    checkFile (file) {
      if (file) {
        const ext = file.split('.')
        if (ext[4] === 'pdf') {
          return true
        } else {
          return false
        }
      }
      return true
    }
  }
}
</script>
